$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.advanceRefer {
    &-header {
        margin: 12px 32px 12px 0;
        display: flex;
        &-refresh {
            margin-left: 12px;
        }
    }
}

