$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.biz-list-header {
  display: flex;
  justify-content: space-between;
  margin-bottom: 2px;
  padding: 12px;
  background-color: #fff;

  &-title {
    margin-bottom: 0;
  }
}