$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.select-table {
  position: relative;

  &-body {
    position: fixed;
    z-index: 9999;
    min-width: 400px;
    max-width: 600px;
    background-color: #f0f2f5;
    box-shadow: 0 3px 6px -4px #0000001f, 0 6px 16px #00000014, 0 9px 28px 8px #0000000d;

    &-list {
      // min-height: 200px;
      max-height: 100%;
      padding: 12px;
      overflow-x: auto;
    }
  }

  &-row {
    cursor: default;
  }

  &-row_selected {
    color: #1890ff;
    background-color: #e6f7ff;
  }

  .mutil-select-input {
    border: 1px solid #d9d9d9;
    border-radius: 2px;
    padding: 3px 4px;
    height: 30px;

    &_disbaled {
      pointer-events: none;
      cursor: not-allowed;
    }

    &_focus {
      border: none;
      width: 2px;
      padding: 0;
      outline: none;
      box-shadow: none;
    }
  }
}
