$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.tagsEditor-plus {
    background: #fff;
    border-style: dashed;
}

.tagsEditor-tag {
    user-select: none;
}

.tagsEditor-input {
    width: 78px;
    margin-right: 8px;
    vertical-align: top;
}