$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.between-wrapper {
    display: flex;
    align-items: center;
}

.between-item {
    flex: 1;

    &-divider {
        margin: 0 8px;
    }
}