$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.quick-query {
    padding: 12px;
    background-color: #ffffff;

    &-item {
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        &_label {}
        &_body {
            display: flex;
        }
    }
    &-actions {
        > button  {
            margin: 0 8px
        }
    }
}