$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.masterForm {
  min-height: 100%;
  display: flex;
  flex: 1;
  flex-direction: column;
  // padding: 8px;
  // overflow: hidden;

  &-body {
    flex: 1;
    // overflow: auto;
  }

  &-footer {
    position: sticky;
    bottom: 0;
    z-index: 1000;
    display: flex;
    justify-content: space-between;
    padding: 12px 8px;
    background: #fff;
    box-shadow: 0 3px 6px -4px #0000001f, 0 6px 16px #00000014, 0 9px 28px 8px #0000000d;

    .actions {
      display: flex;

      button {
        margin-left: 12px;
      }
    }
  }
}