$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.loading-container {
    height: 100%;
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
}