$block-bg: #FFFFFF;
$radius: 4px;

// color
$border-color: #e8e8e8;
// border
$border: 1px solid $border-color;

@mixin blank_page {
    background: $block-bg;
    padding: 16px;
    border: $radius;
}
.queryAction {
  &-item {
    display: flex;
    align-items: center;
    margin: 8px 0;

    &-label {
      width: 100px;
      margin-right: 4px;
    }

    &-body {
      display: flex;
      flex: 1;
    }

    &-valueType {
      min-width: 100px;
    }

    &-values {
      flex: 1;
      margin-left: 12px;
    }
  }
}
